/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */

a {
	color: $color__link;

	&:visited {
		color: $color__link-visited;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

/* Menus */

.main-navigation {
	clear: both;
	display: block;
	float: left;
	width: 100%;

	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-left: 0;

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			float: left;
			position: absolute;
			top: $base__space-unit;
			left: -999rem;
			z-index: 99999;

			ul {
				left: -999rem;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}
	li {
		float: left;
		position: relative;

		&:hover > a,
		&.focus > a {
		}
	}
	a {
		display: block;
		text-decoration: none;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a {
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 768px) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 $base__space-unit;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}

/*--------------------------------------------------------------
# Style
--------------------------------------------------------------*/

.site-header
{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

}

.site-branding{
	position: fixed;
	display: flex;
    z-index: 3;
	background-color: hsla(0,0%,100%,.85);
	transition: background .5s ease-in-out;
	flex-direction: row;
	flex-wrap: wrap;	
	width: 100%;
	border-bottom: 1px solid #e1e1e1;
	padding-left: 5rem;
	padding-right: 5rem;
	height: 50px;
	align-items: center;
}

.main-navigation {
	justify-content: flex-end;
	position: fixed;
	display: flex;
    z-index: 3;
	
	transition: background .5s ease-in-out;
	flex-direction: row;
	flex-wrap: wrap;	
	width: 100%;
	border-bottom: 1px solid #e1e1e1;
	padding-left: 5rem;
	padding-right: 5rem;
	height: 50px;
	align-items: center;
	
	
}

.header-logo {
	width: 50px;
	height: 33px;
}

.menu-main-nav-container {
	display: flex;
	justify-content: flex-end;
		
	a{
		padding-left: 1rem;
		color: $green_brand;
		text-transform: uppercase;
}

}

// search bar

.search-nav{
	border: none;
	padding: 0 0 0 1rem;
	.search-field{
		display: none;
		color: grey;
		background: transparent;
		border: none;
		text-transform: uppercase;
		width: 11.3rem;
		&:focus {
    		outline: none;
		}
	}
	//404
	.search-field-404{
		font-family: $main_fontfamily;
		padding: 0.4rem;
		background-color: #f4f4f4;
		border-radius: 0;
	}
	.search-submit{
		background-color: transparent;
		color: $green_brand;
		padding: 0;
		.icon-search {
			font-size: inherit;
			
		}
	}
	
}